import axios from "../../utils/axios";

//  upToken
export function qiuNiuUpToken(query) {
    return axios({
        url: "/admin/file/upload/qiuNiuUpToken",
        method: "get",
        params: query
    });
}

// 
export function createFile(url, formdata) {
    return axios({
        url: url,
        method: "post",
        data: formdata
    });
}
