            :total="total">
        </el-pagination>

        <!---->
        <el-dialog
            :title="formMap[formName]"
            :visible.sync="formVisible"
            :before-close="hideForm"
            width="85%"
            top="5vh">
            <el-form :model="formData" :rules="formRules" ref="dataForm">
                <el-form-item label="" prop="title">
                    <el-input v-model="formData.title" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pic">
                    <div>
                        <el-input size="small" v-model="formData.pic" auto-complete="off" placeholder=""></el-input>

                        <upload ext="jpg,png,jpeg" :size="716800"  @on-select="onSelectPic"></upload>

                    </div>
                    <div class="upload-img" v-if="formData.pic_url">
                        <img :src="formData.pic_url" style="max-width: 200px;max-height: 200px;">
                    </div>

                </el-form-item>
                <el-form-item label="" prop="describe">
                    <el-input v-model="formData.describe" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="jump_type">
                    <el-radio-group v-model="formData.jump_type">
                        <el-radio :label="0">web </el-radio>
                        <el-radio :label="1">APP</el-radio>
                        <el-radio :label="2"></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item label="URLweb" prop="jump_url" v-if="formData.jump_type === 0 || formData.jump_type === 2">
                    <el-input v-model="formData.jump_url" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="IOS APP" prop="ios_url" v-if="formData.jump_type === 1">
                    <el-input v-model="formData.ios_url" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="Android APP" prop="android_url" v-if="formData.jump_type === 1">
                    <el-input v-model="formData.android_url" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label=" appid" prop="wxa_appid" v-if="formData.jump_type === 2">
                    <el-input v-model="formData.wxa_appid" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="">

                    <el-form-item prop="start_time" style="display: inline-block;">
                        <el-date-picker
                            v-model="formData.start_time"
                            type="datetime"
                            size="mini"
                            placeholder="">
                        </el-date-picker>
                    </el-form-item>
                    -
                    <el-form-item prop="end_time" style="display: inline-block;">
                        <el-date-picker
                            v-model="formData.end_time"
                            type="datetime"
                            size="mini"
                            placeholder="">
                        </el-date-picker>
                    </el-form-item>

                </el-form-item>

                <el-form-item label="" prop="event_name">
                    <el-input v-model="formData.event_name" auto-complete="off"></el-input>
                </el-form-item>

                <el-form-item label="" prop="status">
                    <el-radio-group v-model="formData.status">
                        <el-radio :label="0"></el-radio>
                        <el-radio :label="1"></el-radio>
                    </el-radio-group>
                </el-form-item>

                <el-form-item>
                    <el-button style="float: right;" type="primary" @click.native="formSubmit()" :loading="formLoading"></el-button>
                </el-form-item>

                <el-form-item label="">
                    <div class="remove-list-box">
                        
                        <el-form-item prop="new_show_start_num" style="display: inline-block;">
                            <el-input-number
                                v-model="formData.new_show_start_num"
                                :min="0"
                                controls-position="right"
                                size="mini"
                                label="">
                            </el-input-number>
                        </el-form-item>
                        -
                        
                        <el-form-item prop="new_show_max_num" style="display: inline-block;">
                            <el-input-number
                                v-model="formData.new_show_max_num"
                                :min="0"
                                controls-position="right"
                                size="mini"
                                label="">
                            </el-input-number>
                        </el-form-item>
                    </div>
                </el-form-item>

                <el-form-item label="">
                    <div class="remove-list-box">
                        
                        <el-form-item prop="old_show_start_num" style="display: inline-block;">
                            <el-input-number
                                v-model="formData.old_show_start_num"
                                :min="0"
                                controls-position="right"
                                size="mini"
                                label="">
                            </el-input-number>
                        </el-form-item>
                        -
                        
                        <el-form-item prop="old_show_max_num" style="display: inline-block;">
                            <el-input-number
                                v-model="formData.old_show_max_num"
                                :min="0"
                                controls-position="right"
                                size="mini"
                                label="">
                            </el-input-number>
                        </el-form-item>
                    </div>
                </el-form-item>

                <el-form-item>
                    <span style='color:red;'></span>
                </el-form-item>

                <el-form-item label="" required>
                    <el-form-item prop="channel_type">
                        <el-radio-group v-model="formData.channel_type">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="channel_list" v-if="formData.channel_type !== 0">
                        <div class="remove-list-box">
                            <div class="remove-list">
                                <div class="remove-list-item" v-for="(item, index) in formData.channel_list" :key="index">
                                    {{item}}
                                    <i class="el-icon-close remove-list-close" @click="handleChannelListClose(index)"></i>
                                </div>
                            </div>
                            <div v-show="formData.channel_list.length >= 15" style="color: red;">15</div>
                            <el-input
                                size="mini"
                                v-model="channelListInput"
                                placeholder=""
                                style="width: 190px;"
                                ref="channelListRef"
                                @keyup.enter.native="handleChannelListSubmit"
                                maxlength="12">
                            </el-input>
                            <el-button type="primary" size="mini" @click.native="handleChannelListSubmit()" style="margin-left: 5px;"></el-button>
                        </div>
                    </el-form-item>
                </el-form-item>

                <el-form-item label="Android" required>
                    <el-form-item prop="android_version_type">
                        <el-radio-group v-model="formData.android_version_type">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="android_version_list" v-if="formData.android_version_type !== 0">
                        <div class="remove-list-box">
                            <div class="remove-list">
                                <div class="remove-list-item" v-for="(item, index) in formData.android_version_list" :key="index">
                                    {{item}}
                                    <i class="el-icon-close remove-list-close" @click="handleAndroidVersionListClose(index)"></i>
                                </div>
                            </div>
                            <div v-show="formData.android_version_list.length >= 15" style="color: red;">15</div>
                            <el-input
                                size="mini"
                                v-model="androidVersionListInput"
                                placeholder="Android"
                                style="width: 190px;"
                                ref="androidVersionListRef"
                                @keyup.enter.native="handleAndroidVersionListSubmit"
                                maxlength="12">
                            </el-input>
                            <el-button type="primary" size="mini" @click.native="handleAndroidVersionListSubmit()" style="margin-left: 5px;"></el-button>
                        </div>
                    </el-form-item>
                </el-form-item>

                <el-form-item label="iOS" required>
                    <el-form-item prop="ios_version_type">
                        <el-radio-group v-model="formData.ios_version_type">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                            <el-radio :label="2"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="ios_version_list" v-if="formData.ios_version_type !== 0">
                        <div class="remove-list-box">
                            <div class="remove-list">
                                <div class="remove-list-item" v-for="(item, index) in formData.ios_version_list" :key="index">
                                    {{item}}
                                    <i class="el-icon-close remove-list-close" @click="handleIosVersionListClose(index)"></i>
                                </div>
                            </div>
                            <div v-show="formData.ios_version_list.length >= 15" style="color: red;">15</div>
                            <el-input
                                size="mini"
                                v-model="iosVersionListInput"
                                placeholder="iOS"
                                style="width: 190px;"
                                ref="iosVersionListRef"
                                @keyup.enter.native="handleIosVersionListSubmit"
                                maxlength="12">
                            </el-input>
                            <el-button type="primary" size="mini" @click.native="handleIosVersionListSubmit()" style="margin-left: 5px;"></el-button>
                        </div>
                    </el-form-item>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="hideForm"></el-button>
                <el-button type="primary" @click.native="formSubmit()" :loading="formLoading"></el-button>
            </div>
        </el-dialog>

    </div>

</template>

<script>
import { adList, adSave, adDelete } from "../../api/ad/ad";
import Upload from "../../components/File/Upload.vue";
const formJson = {
    ad_id: "",
    title: "",
    describe: "",
    pic: "",
    pic_url: "",
    jump_type: "",
    jump_url: "",
    ios_url: "",
    android_url: "",
    wxa_appid: "",
    channel_type: 0,
    channel_list: [],
    android_version_type: 0,
    android_version_list: [],
    ios_version_type: 0,
    ios_version_list: [],
    new_show_start_num: 0,
    new_show_max_num: 0,
    old_show_start_num: 0,
    old_show_max_num: 0,
    end_time: "",
    event_name: "",
    status: 1
};
export default {
    data() {
        return {
            query: {
                title: "",
                page: 1,
                limit: 20
            },
            channelListInput: "",
            androidVersionListInput: "",
            iosVersionListInput: "",
            list: [],
            total: 0,
            loading: true,
            index: null,
            formName: null,
            formMap: {
                add: "",
                edit: ""
            },
            formLoading: false,
            formVisible: false,
            formData: formJson,
            formRules: {
                title: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                pic: [
                    {
                        required: true,
                        message: "",
                        trigger: "change"
                    }
                ],
                jump_type: [
                    {
                        required: true,
                        message: "",
                        trigger: "change"
                    }
                ],
                jump_url: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    }
                ],
                android_url: [
                    {
                        required: true,
                        message: "Android",
                        trigger: "blur"
                    }
                ],
                ios_url: [
                    {
                        required: true,
                        message: "IOS",
                        trigger: "blur"
                    }
                ],
                wxa_appid: [
                    {
                        required: true,
                        message: "Appid",
                        trigger: "blur"
                    }
                ],
                channel_list: [
                    {
                        required: true,
                        message: "",
                        trigger: "change"
                    }
                ],
                android_version_list: [
                    {
                        required: true,
                        message: "Android",
                        trigger: "change"
                    }
                ],
                ios_version_list: [
                    {
                        required: true,
                        message: "iOS",
                        trigger: "change"
                    }
                ],
                status: [
                    { required: true, message: "", trigger: "change" }
                ]
            },
            deleteLoading: false
        };
    },
    components: {
        Upload
    },
    methods: {
        onReset() {
            this.$router.push({
                path: ""
            });
            this.query = {
                title: "",
                page: 1,
                limit: 20
            };
            this.getList();
        },
        onSubmit() {
            this.$router.push({
                path: "",
                query: this.query
            });
            this.getList();
        },
        handleSizeChange(val) {
            this.query.limit = val;
            this.getList();
        },
        handleCurrentChange(val) {
            this.query.page = val;
            this.getList();
        },
        getList() {
            this.loading = true;
            adList(this.query)
                .then(response => {
                    this.loading = false;
                    this.list = response.data.list || [];
                    this.total = response.data.total || 0;
                })
                .catch(() => {
                    this.loading = false;
                    this.list = [];
                    this.total = 0;
                });
        },
        // 
        handleChannelListClose(index) {
            console.log(index);
            // 
            this.formData.channel_list.splice(index, 1);
        },
        // 
        handleChannelListSubmit() {
            // 
            if (
                this.channelListInput &&
                this.formData.channel_list.length < 15
            ) {
                let arr = this.channelListInput.split(",");
                for (let i in arr) {
                    if (
                        arr[i] !== "" &&
                        (this.formData.channel_list.length === 0 ||
                            this.formData.channel_list.indexOf(arr[i]) < 0)
                    ) {
                        this.formData.channel_list.push(arr[i]);
                    }
                }
                this.channelListInput = "";
                this.$refs.channelListRef.focus();
            }
        },
        // Android
        handleAndroidVersionListClose(index) {
            // 
            this.formData.android_version_list.splice(index, 1);
        },
        // Android
        handleAndroidVersionListSubmit() {
            // 
            if (
                this.androidVersionListInput &&
                this.formData.android_version_list.length < 15
            ) {
                let arr = this.androidVersionListInput.split(",");
                for (let i in arr) {
                    let avList = this.formData.android_version_list;
                    if (
                        arr[i] !== "" &&
                        (avList.length === 0 || avList.indexOf(arr[i]) < 0)
                    ) {
                        this.formData.android_version_list.push(arr[i]);
                    }
                }
                this.androidVersionListInput = "";
                this.$refs.androidVersionListRef.focus();
            }
        },
        // Ios
        handleIosVersionListClose(index) {
            // 
            this.formData.ios_version_list.splice(index, 1);
        },
        // Ios
        handleIosVersionListSubmit() {
            // 
            if (
                this.iosVersionListInput &&
                this.formData.ios_version_list.length < 15
            ) {
                let arr = this.iosVersionListInput.split(",");
                for (let i in arr) {
                    if (
                        arr[i] !== "" &&
                        (this.formData.ios_version_list.length === 0 ||
                            this.formData.ios_version_list.indexOf(arr[i]) < 0)
                    ) {
                        this.formData.ios_version_list.push(arr[i]);
                    }
                }
                this.iosVersionListInput = "";
                this.$refs.iosVersionListRef.focus();
            }
        },
        // 
        resetForm() {
            if (this.$refs["dataForm"]) {
                // 
                this.$refs["dataForm"].resetFields();
                // 
                this.$refs["dataForm"].clearValidate();
            }
        },
        // 
        hideForm() {
            // 
            this.formVisible = !this.formVisible;
            return true;
        },
        // 
        handleForm(index, row) {
            this.formVisible = true;
            // 
            this.resetForm();
            this.formData = JSON.parse(JSON.stringify(formJson));
            if (row !== null) {
                this.formData = Object.assign({}, row);
            }
            this.formName = "add";
            if (index !== null) {
                this.index = index;
                this.formName = "edit";
            }
        },
        formSubmit() {
            this.$refs["dataForm"].validate(valid => {
                if (valid) {
                    this.formLoading = true;
                    let data = Object.assign({}, this.formData);
                    adSave(data, this.formName)
                        .then(response => {
                            this.formLoading = false;
                            if (response.code) {
                                this.$message.error(response.message);
                                return false;
                            }
                            this.$message.success("");
                            this.formVisible = false;
                            if (this.formName === "add") {
                                // 
                                data.ad_id = response.data.ad_id;
                                this.list.unshift(data);
                            } else {
                                this.list.splice(this.index, 1, data);
                            }
                        })
                        .catch(() => {
                            this.formLoading = false;
                        });
                }
            });
        },
        // 
        handleDel(index, row) {
            if (row.ad_id) {
                this.$confirm("?", "", {
                    type: "warning"
                })
                    .then(() => {
                        let para = { ad_id: row.ad_id };
                        this.deleteLoading = true;
                        adDelete(para)
                            .then(response => {
                                this.deleteLoading = false;
                                if (response.code) {
                                    this.$message.error(response.message);
                                    return false;
                                }
                                this.$message("");
                                // 
                                this.list.splice(index, 1);
                            })
                            .catch(() => {
                                this.deleteLoading = false;
                            });
                    })
                    .catch(() => {
                        this.$message.info("");
                    });
            }
        },
        onSelectPic(filePath, filePathUrl) {
            this.formData.pic = filePath;
            this.formData.pic_url = filePathUrl;
        }
    },
    filters: {
        statusFilterType(status) {
            const statusMap = {
                0: "gray",
                1: "success"
            };
            return statusMap[status];
        },
        statusFilterName(status) {
            const statusMap = {
                0: "",
                1: ""
            };
            return statusMap[status];
        }
    },
    mounted() {},
    created() {
        // 
        let query = this.$route.query;
        this.query = Object.assign(this.query, query);
        this.query.limit = parseInt(this.query.limit);
        // 
        this.getList();
    }
};
</script>

<style type="text/scss" lang="scss">
</style>
